import React from 'react'

export default function IncomeExpense() {
    return (
        <div className="status-container">
            <div className="income">
                <div>INCOME</div>
                <div>$299</div>
            </div>
            <div className="expense">
                <div>EXPENSE</div>
                <div>$255</div>
            </div>
        </div>
    )
}
